<script>
import vue, {lib, library} from "vue.min.js"
export default vue.next ({
	name: "date:time",
	properties: ["format", "stamp", "offset"],
	setup: function (properties) {
		var format = vue.reference (properties.format)
		var stamp = vue.reference (lib.to.number (properties.stamp))
		var offset = vue.reference (properties.offset)
		vue.watch (properties, function () {
			format.value = properties.format
			stamp.value = lib.to.number (properties.stamp)
			offset.value = properties.offset
			})
		return {vue, format, stamp, offset}
		},
	})
</script>

<template name="date:time">
	<span element="date:time" pixel-view>{{ vue.date.format (format, stamp, offset) }}</span>
</template>
