<script>
import vue, {lib, library} from "vue.min.js"
export default vue.next ({
	name: "button:material",
	properties: ["icon", "icon-style", "icon-position"],
	setup: function (properties) {
		var icon = vue.reactive ({src: properties.icon, style: properties [vue.prop ("icon-style")], position: properties [vue.prop ("icon-position")]})
		vue.watch (properties, function () {
			icon.src = properties.icon
			icon.style = properties [vue.prop ("icon-style")]
			icon.position = properties [vue.prop ("icon-position")]
			})
		return {vue, icon}
		},
	method: {
		function () {},
		slot () { return vue.slot (this).default },
		},
	})
</script>

<template name="button:material">
	<button v-if="icon.position === 'end'" element="button:material">
		<div:text v-if="slot ()"><slot slot/></div:text>
		<icon:material v-if="icon.src" v-bind:src="icon.src" v-bind:style-sheet="icon.style"/>
	</button>
	<button element="button:material" v-else>
		<icon:material v-if="icon.src" v-bind:src="icon.src" v-bind:style-sheet="icon.style"/>
		<div:text v-if="slot ()"><slot slot/></div:text>
	</button>
</template>

<style lang="less">
@import "/src/style.less";
@import "/src/style/sheet.less";
@box-shadow-begin: 0px 1px 2px 0px;
@box-shadow-end: 0px 1px 3px 0px;
[element="button:material"] {
	.flex-box-line;
	.font-bold-light;
	.text-shadow;
	.border-radius;
	color: #666666;
	gap: @px;
	padding: @px @px-small;
	border: @border solid #999999;
	box-shadow: @box-shadow-begin #CCCCCC, @box-shadow-end #EEEEEE;
	background-image: linear-gradient(to top right, #F1F2F3, #FEFDFC);
	}
[element="button:material"]:hover { background-image: linear-gradient(to top right, #FEFDFC, #F1F2F3); }
[element="button:material"]:active { border-color: #888888; }
[element="button:material"][border-radius="sky"] { .border-radius-sky; }
[element="button:material"][border-radius="regular"] { .border-radius-regular; }
[element="button:material"][border-radius="round"] { .border-radius-round; padding: @px @px-medium; }
[element="button:material"][border-radius="corner"] { .border-radius-corner; }
</style>
