<script>
import vue, {lib, library} from "vue.min.js"
export default vue.next ({
	name: "form:date",
	properties: ["for", "model", "complete", "capitalize", "correct", "spell"],
	setup: function (properties) {
		var label = vue.reference (properties.for)
		var model = vue.reference (vue.model.format (properties.model))
		var property = vue.reactive (vue.form.attribute (properties))
		vue.watch (properties, function () {
			label.value = properties.for
			model.value = vue.model.format (properties.model)
			vue.react (property, vue.form.attribute (properties))
			})
		return {vue, label, model, property}
		},
	})
</script>

<template name="form:date">
	<input v-if="label" v-bind:for="label" v-model="vue.model [model]" v-on:focus="vue.form.focus" v-on:blur="vue.form.blur" v-bind:autocomplete="property.complete" v-bind:autocapitalize="property.capitalize" v-bind:autocorrect="property.correct" v-bind:spellcheck="property.spell" type="date" element="form:date">
	<input v-model="vue.model [model]" v-bind:autocomplete="property.complete" v-bind:autocapitalize="property.capitalize" v-bind:autocorrect="property.correct" v-bind:spellcheck="property.spell" type="date" element="form:date" v-else>
</template>
