<script>
import vue, {lib, library} from "vue.min.js"
export default vue.next ({
	name: "form:check",
	properties: ["for", "model"],
	setup: function (properties) {
		var label = vue.reference (properties.for)
		var model = vue.reference (vue.model.format (properties.model))
		vue.watch (properties, function () {
			label.value = properties.for
			model.value = vue.model.format (properties.model)
			})
		return {vue, label, model}
		},
	})
</script>

<template name="form:check">
	<input v-if="label" v-bind:for="label" v-on:focus="vue.form.focus" v-on:blur="vue.form.blur" v-model="vue.model [model]" type="checkbox" element="form:check">
	<input v-model="vue.model [model]" type="checkbox" element="form:check" v-else>
</template>
