import vue, {lib, library} from "vue.min.js"
import ini from "/src/package.json"
let {define} = lib
let {zero, one} = lib

var session = lib.object.concat ({name: lib.session.name, signature: lib.session.signature, mode: lib.session.mode, secure: lib.session.secure}, ini.session)
var name = lib.storage.set ("session:name", (session.name || lib.session.name), true)
var signature = lib.storage.set ("session:signature", (session.signature || lib.session.signature), true)
var mode = lib.storage.set ("session:mode", (session.mode || lib.session.mode), true)
var secure = lib.storage.set ("session:secure", (session.secure || lib.session.secure), true)
vue.session = new lib.session.manager
vue.session.start ({name, signature, mode, secure}, function (id) {})
vue.session.check = function () {
	var session = zero
	if (vue.package.session) {
		if ((vue.package.session.name === lib.storage.get ("session:name")) === false) if (lib.storage.set ("session:name", vue.package.session.name)) session ++
		if ((vue.package.session.signature === lib.storage.get ("session:signature")) === false) if (lib.storage.set ("session:signature", vue.package.session.signature)) session ++
		if ((vue.package.session.mode === lib.storage.get ("session:mode")) === false) if (lib.storage.set ("session:mode", vue.package.session.mode)) session ++
		if ((vue.package.session.secure === lib.storage.get ("session:secure")) === false) if (lib.storage.set ("session:secure", vue.package.session.secure)) session ++
		}
	if (session) vue.router.go ()
	return session
	}
