import vue, {lib, library} from "vue.min.js"
import ini from "/src/package.json"
let {define} = lib
let {zero, one} = lib

vue.form = function () {}
vue.form.focus = function (element) { lib.dom (lib.query.selector ({id: lib.dom (element.target).attribute ("for")})).class.insert ("--focus") }
vue.form.blur = function (element) { lib.dom (lib.query.selector ({id: lib.dom (element.target).attribute ("for")})).class.delete ("--focus") }
vue.form.attribute = function (attribute) {
	return {
		complete: lib.to_bool (attribute.complete),
		capitalize: lib.to_bool (attribute.capitalize),
		correct: lib.to_bool (attribute.correct),
		spell: lib.to_boolean (attribute.spell),
		}
	}
