import {createApp, toRefs, ref, reactive, beforeMount, onMounted, onUnmounted, onUpdated, computed, watch, watchEffect, inject} from "vue";
import {useRoute, useRouter, createRouter, createWebHistory, createWebHashHistory} from "vue-router";
import {createStore, useStore} from "vuex";
import axios from "axios";

function route () { return useRoute (); }
function router () { return useRouter (); }
router.create = createRouter;
router.history = createWebHistory;
router.hash = {tag: createWebHashHistory}
router.try = {catch: "/:catchAll(.*)"}

function emit (key, ... value) { if ("list" in Function.event) if (Function.event.list [key]) for (var i in Function.event.list [key]) Function.event.list [key][i].call (null, ... value); }
function on (key, value) {
	if (key === "mount") return onMounted (value);
	if (key === "un:mount") return onUnmounted (value);
	if (key === "update") return onUpdated (value);
	if ("list" in Function.event) { if ((key in Function.event.list) === false) Function.event.list [key] = []; Function.event.list [key].push (value); }
	}
on.line = ref (true)

function properties (app, key, value) { app.config.globalProperties [key] = value; }
function slot (proto) { return proto.$slots; }
function component (... param) { return new component.__ (... param); }
function element (... param) { return new element.__ (... param); }

function respond () {}
function response () {}
function request () {}
request.get = function (... param) { return axios.get (... param); }
request.post = function (... param) { return axios.post (... param); }
request.url = process.env.BASE_URL;

function reference (input = null) { return ref (input); }
function react (key, value) { for (var i in value) key [i] = value [i]; }
react.clear = function (reactor) { for (var i in reactor) delete reactor [i]; }

function store (x) { return createStore ({strict: x.strict || true, state: x.state || {}, mutations: x.mutation || {}, actions: x.action || {}}); }
store.create = createStore;
store.use = useStore;

export default {
	application: createApp,
	properties, to: {reference: toRefs}, reference, react, reactive: function (input = {}) { return reactive (input); },
	compute: computed, watch, watching: watchEffect, inject, store,
	route, router,
	on, emit,
	process: {mode: process.env.NODE_ENV}, request, response, respond,
	slot, component, element,
	directive: function (app, directive, vue) {
		if (Object.is.function (vue)) app.directive (directive, {beforeMount: vue, updated: vue});
		else app.directive (directive, {beforeMount: vue.mount, updated: vue.update});
		},
	next: function (vue) {
		return {
			name: vue.name || "",
			props: vue.properties || [],
			data (properties) { return {} },
			setup (properties) { if (vue.setup) return vue.setup.call (this, properties) },
			mounted () { if (vue.mount) vue.mount.call (this) },
			unmounted () { if (vue.eject) vue.eject.call (this) },
			created () { if (vue.create) vue.create.call (this) },
			updated () { if (vue.update) vue.update.call (this) },
			methods: vue.method,
			}
		},
	}
