import vue, {lib, library} from "vue.min.js"
import ini from "/src/package.json"
let {define} = lib
let {zero, one} = lib

vue.date = vue.reactive ({
	start: function () {
		lib.date.universal (lib.date.zone ["universal"].offset)
		vue.date.set ("time:zone", vue.session.get ("time:zone"), {session: true})
		vue.date.routine ()
		vue.date.cache = lib.time.interval (vue.date.routine)
		},
	routine: function () {
		vue.date.create = lib.date.create ({zone: vue.time.zone.offset})
		vue.date.time = vue.date.create (vue.time.stamp = lib.time.stamp ())
		vue.date.time.use ("month:name", vue.language ("date-time:month"))
		vue.date.time.use ("day:name", vue.language ("date-time:day"))
		var format = lib.date.format ()
		for (var i in format) vue.date.get [i] = vue.date.time.format (format [i])
		vue.emit ("date:routine")
		},
	format: function (... date) {
		var format = "default", offset, time = vue.time.stamp
		for (var i in date) {
			if (lib.is.string (date [i])) if (date [i]) format = date [i]
			if (lib.is.number (date [i])) if (date [i]) time = date [i]
			if (lib.is.object (date [i])) offset = date [i]
			}
		return vue.date.create (time).format (format, offset)
		},
	transform: function (format, ... date) {
		return vue.date.create (format).format (... date)
		},
	factory: function (date) {
		date = date.string ()
		var year = date.substr (0, 4)
		var month = date.substr (4, 2)
		var day = date.substr (6, 2)
		return [day, month, year].join ("/")
		},
	set: function (key, value, ... argument) {
		var argument = lib.function.argument (... argument), option = (argument.object || {}), context = argument.function
		if (key === "time:zone") {
			if (value in lib.time.zone || value === "universal") {
				vue.time.zone = {name: vue.session.set ("time:zone", value, option.session)}
				vue.time.zone.offset = lib.time.zone (vue.time.zone.name).offset
				vue.socket.emit ("time:zone", vue.time.zone.name).next (context)
				if (null) vue.socket.emit ("session", {time: {zone: vue.time.zone.name}}).next (context)
				}
			}
		},
	get: vue.reactive (),
	})

vue.time = vue.reactive ({
	stamp: lib.time.stamp (),
	zone: {name: "universal", offset: lib.date.zone ["universal"].offset},
	})
