import vue, {lib, library} from "vue.min.js"
import ini from "/src/package.json"
let {define} = lib
let {zero, one} = lib

const fire = {
	base: function (config, url = vue.socket.io.url) {
		vue.fire = {base: new lib.plugin.google.fire.base}
		vue.fire.base.start (config, url)
		},
	}

vue.db.fire = function () {}
vue.db.fire.base = function (context) {
	if (vue.package.fire) {
		if (vue.package.fire.base) {
			fire.base (vue.package.fire.base)
			if (context) context.call ()
			if (vue.db.fire.base.on.start.length) vue.db.fire.base.emit ("start")
			}
		}
	}
vue.db.fire.base.emit = function (key) { for (var i in vue.db.fire.base.on [key]) vue.db.fire.base.on [key][i].call () }
vue.db.fire.base.on = function (key, value) { if (key === "start") vue.db.fire.base.on.start.push (value) }
vue.db.fire.base.on.start = []

if (ini.fire) {
	if (ini.fire.base) {
		fire.base (ini.fire.base)
		}
	}
