<script>
import vue, {lib, library} from "vue.min.js"
export default vue.next ({
	name: "icon:material",
	properties: ["src", "style-sheet"],
	setup: function (properties) {
		var src = vue.reference (icon [properties.src] || properties.src)
		var style = vue.reference (properties [vue.prop ("style-sheet")] || material.style.name), sheet
		if (style.value) sheet = [material.css, (material.style.sheet [style.value] || style.value)].join ("-")
		else sheet = material.css
		var css = vue.reference (sheet)
		vue.watch (properties, function () {
			src.value = icon [properties.src] || properties.src
			if (style.value = (properties [vue.prop ("style-sheet")] || material.style.name)) css.value = [material.css, (material.style.sheet [style.value] || style.value)].join ('-')
			})
		return {vue, css, src}
		},
	})
var material = {
	css: ("material-icon").concat ("s"),
	style: {
		name: null,
		sheet: {
			line: ("out").concat ("line").concat ("d"),
			tone: ("two").concat ("-").concat ("tone"),
			},
		},
	}
var icon = {
	icon: "",
	}
</script>

<template name="icon:material">
	<span v-bind:class="css" element="icon:material" icon>{{ src }}</span>
</template>

<style lang="less">
@import "/src/style.less";
@import "/src/style/sheet.less";
[element="icon:material"] {
	.flex;
	.align-item;
	.justify-content;
	font-size: 135%;
	}
</style>
