import vue, {lib, library} from "vue.min.js"
import ini from "/src/package.json"
let {define} = lib
let {zero, one} = lib

var socket = {
	url: lib.dom.attribute ("meta[property=socket:url]", "content") || ini ["socket:url"] || "",
	token: lib.dom.attribute ("meta[property=socket:token]", "content") || ini ["socket:token"] || "",
	key: null,
	value: null,
	option: function () {
		this.http = {url: socket.url}
		this.url = lib.web.socket.url (socket.url)
		this.token = socket.token
		this.session = vue.session.id
		},
	}

vue.socket = new lib.web.socket.io (socket.option)
vue.socket.ready = vue.reference ()
vue.socket.io = socket
vue.socket.get = function (path, header) { return vue.fetch.get (this.http.url.concat (path), lib.object.concat ({token: this.token}, header)) }
vue.socket.post = function (path, data, header) { return vue.fetch.post (this.http.url.concat (path), data, lib.object.concat ({token: this.token}, header)) }

if (vue.socket.start ()) {
	vue.socket.connect ()
	vue.socket.connect (function () { vue.on.line.value = true })
	vue.socket.connect ("error", function () { vue.on.line.value = null })
	}

if (null) {
	vue.fetch.get ("http://sub.local.host/package.json", {token: "token", session: vue.session.id})
	. then (function (response) {
		console.log (response.data)
		})
	. catch (function (error) {
		console.error (error)
		})
	}
