import vue, {lib, library} from "vue.min.js"
import dom from "vue.min.js/plugin/dom"

export default {
	install: function (app, option) {
		app.directive ("element", function (element, attribute) {})
		app.directive ("component", function (element, attribute) {})
		if (null) vue.directive (app, "auto-complete", function (element, attribute) { lib.dom (element).attribute ("autocomplete", lib.to_bool (attribute.value)) })
		if (null) vue.directive (app, "auto-capitalize", function (element, attribute) { lib.dom (element).attribute ("autocapitalize", lib.to_bool (attribute.value)) })
		if (null) vue.directive (app, "auto-correct", function (element, attribute) { lib.dom (element).attribute ("autocorrect", lib.to_bool (attribute.value)) })
		if (null) vue.directive (app, "spell-check", function (element, attribute) { lib.dom (element).attribute ("spellcheck", lib.to_boolean (attribute.value)) })
		for (var i in dom) app.component (i, dom [i])
		}
	}
