<script>
import vue, {lib, library} from "vue.min.js"
export default vue.next ({
	name: "container",
	properties: ["container"],
	setup: function (properties) {
		var container = vue.reference (properties.container)
		vue.watch (properties, function () { container.value = properties.container })
		return {vue, container}
		},
	})
</script>

<template name="container">
	<div v-if="container" id="container">
		<slot slot/>
	</div>
</template>
