import vue, {lib, library} from "vue.min.js"
import ini from "/src/package.json"
let {define} = lib
let {zero, one} = lib

window.url = lib.url.parse (lib.string (window.location))
vue.url = vue.reactive ()
vue.url.param = function (param) { return vue.route [("param").concat ("s")][param] }
vue.url.previous = function (i = one) { vue.router.go (- i) }
vue.url.go = function (url, option) {
	if (url === true) return vue.router.go ()
	if (lib.is.object (url)) return vue.router.push (url)
	if (url.begin () === "/") return vue.router.push (url)
	else return vue.router.push (lib.object.concat ({name: url}, option))
	}

vue.parse_url = function (update = true) {
	var protocol = window.location.protocol.pop ()
	var host = window.location.host
	var path = vue.route.path
	var query = lib.url.query.format (vue.route.query)
	var url = lib.url.parse ((protocol).concat ("://").concat (host).concat (path).concat (query))
	for (var i in url) vue.url [i] = url [i]
	if (null) if (update) vue.on ("update", function () { vue.parse_url (null) })
	}

vue.fetch = function (... url) {
	return vue.fetch.get (... url)
	}

vue.fetch.get = function (url, header) {
	url = [url]
	if (header) url.push ({[("header").concat ("s")]: header})
	return vue.request.get (... url)
	}

vue.fetch.post = function (url, data, header) {
	return vue.request.post (url, data, header)
	}
