import vue, {lib, library} from "vue.min.js"
import ini from "/src/package.json"
let {define} = lib
let {zero, one} = lib
var setup = vue.reference ()

vue.start = function (context, option = {delay: true}) {
	vue.route = lib.route ()
	vue.router = lib.router ()
	vue.x = vue.store.use ()
	vue.date.start ()
	if (vue.visitor = vue.reactive (lib.visitor ())) {
		lib.dom.attribute ("body", "device", vue.visitor.device.name)
		lib.dom.attribute ("body", "browser", vue.visitor.browser.platform)
		if (["computer"].exist (vue.visitor.device.name)) vue.computer = true
		else vue.mobile = true
		}
	vue.watch (vue.socket.ready, function (status) {
		vue.socket.emit ("package").catch (function (error) { vue.emit ("web-socket:error", error) }).then (function (response) {
			vue.react (vue.package, lib.object.merge (ini, response.package))
			vue.react (vue.visitor, response.visitor)
			if ("session") {
				if (vue.session.check ()) return
				else vue.session.apply (response.session)
				}
			if ("language") {
				if (vue.package.language) vue.language.list.value = vue.package.language.list
				vue.language.apply (response.language)
				}
			if ("date") {
				if (vue.package.date) {
					for (var i in vue.package.date.format) {
						lib.date.format (i, vue.package.date.format [i])
						}
					}
				}
			if ("account") {
				// vue.account.start ()
				vue.db.fire.base.on ("start", function () {
					// vue.account.start (vue.fire.base)
					})
				}
			vue.db.setup ()
			vue.db.fire.base ()
			vue.emit ("web-socket")
			if (context) context.call (vue)
			if (vue.ping || (vue.ping = function () {})) vue.ping.cache = lib.time.interval (function () { vue.emit ("ping") }, (vue.ping.interval = vue.package.ping.interval))
			if (option.delay === null) lib.time.sleep (function () { vue.ready.value = true })
			else vue.ready.value = true
			setup.value = true
			})
		})
	lib.time.sleep (function () { if (lib.un (setup.value)) vue.emit ("web-socket:error") }, (one + one + one))
	vue.emit ("setup")
	}

if (vue.system = function () {})
if (vue.system.ready = vue.reference ())
if (vue.ready = vue.reference ()) vue.package = vue.reactive ()
vue.import = function () {}
vue.export = function (data, setup) {
	if (lib.is.function (data)) if (setup = data) data = lib.un.define ()
	if (vue.system.ready.value) if (setup) setup.call ()
	vue.watch (vue.ready, function (ready) { if (ready) if (setup) if (vue.system.ready.value = true) setup.call () })
	return lib.object.concat ({vue}, data)
	}

vue.model = vue.reactive ({
	format: function (model) {
		var prefix = ("vue.model") + (".")
		if ((model = lib.to.string (model) || "*").begin (prefix)) return model.shift (prefix)
		return model
		},
	})

vue.layout = function () {
	if (vue.route.meta ["layout"]) {
		var layout = ("layout").concat (":").concat (vue.route.meta ["layout"])
		if (vue.partial.exist (layout)) return layout
		else return "layout:slot"
		}
	else return "layout:slot"
	}

vue.prop = function (property) {
	var properties = []
	for (var i in (property = property.split ("-"))) {
		if (i == zero) properties.push (property [i])
		else properties.push (property [i].uc_begin ())
		}
	return properties.join ("")
	}

vue.prop.s = function (... property) {
	return property.join ("-")
	}

vue.commit = function (context) { return vue.x.commit ("mutation", context) }
vue.dispatch = function (context) { return vue.x.dispatch ("action", context) }
