import firebase from "firebase/compat/app";
import "firebase/compat/auth";
import "firebase/compat/firestore";
import {Loader} from "@googlemaps/js-api-loader";

Function.define (Function.plugin.google.fire.base, "link", firebase, {configurable: true, enumerable: true, writable: true});
Function.define (Function.plugin.google.map, "loader", function (apiKey) { return new Loader ({apiKey: apiKey}); });
