<script>
import vue, {lib, library} from "vue.min.js"
export default vue.next ({
	name: "a:link",
	properties: ["url"],
	setup: function (properties) {
		var url = vue.reference (router (properties.url))
		vue.watch (properties, function () { url.value = router (properties.url) })
		return {vue, url}
		},
	})
function router (url) {
	if (lib.is.string (url)) {
		if (false === (url.begin () === "/")) {
			return {name: url}
			}
		}
	return url
	}
</script>

<template name="a:link">
	<router-link v-if="url" v-bind:to="url"><slot slot/></router-link>
	<a v-else><slot slot/></a>
</template>
